"use client"

import { useCallback, useEffect, useMemo, useState } from "react"
import Link from "next/link"
import {
  AlertCircle,
  Building2,
  Check,
  ChevronLeft,
  ChevronRight,
  Download as DownloadIcon,
  Layers,
  Loader2,
  RefreshCcw,
  ShieldCheck,
  Users,
  X as XIcon,
} from "lucide-react"

import { authService, type AdminTeamSummary } from "@/lib/auth-service"
import { requestsService, type RequestData, type RequestsListResponse } from "@/lib/requests-service"
import {
  approveDocument,
  getDocumentDashboardOverview,
  getDownloadURL,
  getTeamDocumentHistory,
  rejectDocument,
  type DocumentOverviewItem,
  type DocumentType,
  type TeamDocumentHistoryItem,
} from "@/utils/upload"
import { useToast } from "@/components/ui/use-toast"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { RequestCategoryBadge } from "@/components/request-category-badge"
import { RequestStatusBadge } from "@/components/request-status-badge"
import { StatusChangeModal } from "@/components/modals/status-change-modal"

interface AdminDashboardProps {
  user: Record<string, any> | null
}

interface QueueSummary {
  results: RequestData[]
  count: number
}

interface RejectContext {
  versionId: string
  teamName: string
  documentLabel: string
}

const REQUESTS_ORDERING = "-created_at"

export function AdminDashboard({ user }: AdminDashboardProps) {
  const { toast } = useToast()

  const [loading, setLoading] = useState(true)
  const [refreshing, setRefreshing] = useState(false)
  const [errors, setErrors] = useState<string[]>([])

  const [teams, setTeams] = useState<AdminTeamSummary[]>([])
  const [queue, setQueue] = useState<QueueSummary>({ results: [], count: 0 })
  const [documents, setDocuments] = useState<DocumentOverviewItem[]>([])

  const [requestsPage, setRequestsPage] = useState(1)
  const [requestsResponse, setRequestsResponse] = useState<RequestsListResponse | null>(null)
  const [requestsLoading, setRequestsLoading] = useState(false)
  const [requestsError, setRequestsError] = useState<string | null>(null)

  const [selectedTeamId, setSelectedTeamId] = useState<string>("")
  const [teamDocuments, setTeamDocuments] = useState<TeamDocumentHistoryItem[]>([])
  const [teamDocsLoading, setTeamDocsLoading] = useState(false)
  const [teamDocsError, setTeamDocsError] = useState<string | null>(null)

  const [docActionId, setDocActionId] = useState<string | null>(null)
  const [downloadingId, setDownloadingId] = useState<string | null>(null)
  const [rejectDialog, setRejectDialog] = useState<RejectContext | null>(null)
  const [rejectRemarks, setRejectRemarks] = useState("")
  const [statusDialog, setStatusDialog] = useState<{ request: RequestData; targetStatus: string } | null>(null)
  const [statusLoadingIds, setStatusLoadingIds] = useState<number[]>([])

  const loadHeadline = useCallback(async () => {
    setErrors([])
    setRefreshing(true)
    try {
      const [teamsResult, queueResult, documentsResult] = await Promise.allSettled([
        authService.getAdminTeams(),
        requestsService.getFilteredQueueSnapshot({ include_positions: true, page: 1, page_size: 12 }),
        getDocumentDashboardOverview(),
      ])

      if (teamsResult.status === "fulfilled") {
        setTeams(teamsResult.value || [])
      } else {
        console.error("Admin teams load failed", teamsResult.reason)
        setErrors((prev) => [...prev, "Unable to load team summaries."])
      }

      if (queueResult.status === "fulfilled") {
        setQueue({ results: queueResult.value.results || [], count: queueResult.value.count || 0 })
      } else {
        console.error("Queue snapshot load failed", queueResult.reason)
        setErrors((prev) => [...prev, "Unable to load virtual queue data."])
      }

      if (documentsResult.status === "fulfilled") {
        setDocuments(documentsResult.value || [])
      } else {
        console.error("Pending documents load failed", documentsResult.reason)
        setErrors((prev) => [...prev, "Unable to load pending documents."])
      }
    } catch (error) {
      console.error("Headline load failure", error)
      setErrors((prev) => [...prev, "Unable to refresh dashboard data."])
    } finally {
      setLoading(false)
      setRefreshing(false)
    }
  }, [])

  const loadRequests = useCallback(
    async (page: number) => {
      setRequestsLoading(true)
      setRequestsError(null)
      try {
        const response = await requestsService.listRequests({ page, ordering: REQUESTS_ORDERING })
        setRequestsResponse(response)
      } catch (error) {
        console.error("Admin requests load failed", error)
        const message = error instanceof Error ? error.message : "Unable to load requests."
        setRequestsError(message)
      } finally {
        setRequestsLoading(false)
      }
    },
    [],
  )

  const loadTeamDocuments = useCallback(async (teamId: string) => {
    if (!teamId) {
      setTeamDocuments([])
      return
    }
    setTeamDocsLoading(true)
    setTeamDocsError(null)
    try {
      const history = await getTeamDocumentHistory(teamId)
      setTeamDocuments(history)
    } catch (error) {
      console.error("Team documents load failed", error)
      const message = error instanceof Error ? error.message : "Unable to load documents for the team."
      setTeamDocsError(message)
      setTeamDocuments([])
    } finally {
      setTeamDocsLoading(false)
    }
  }, [])

  useEffect(() => {
    loadHeadline()
  }, [loadHeadline])

  useEffect(() => {
    loadRequests(requestsPage)
  }, [requestsPage, loadRequests])

  useEffect(() => {
    if (teams.length > 0 && !selectedTeamId) {
      setSelectedTeamId(teams[0].id)
    }
  }, [teams, selectedTeamId])

  useEffect(() => {
    if (selectedTeamId) {
      loadTeamDocuments(selectedTeamId)
    }
  }, [selectedTeamId, loadTeamDocuments])

  const shouldCollectRemarks = useCallback((currentStatus: string, nextStatus: string) => {
    if (nextStatus === "Cannot be Processed") return true
    return currentStatus === "Processing" && nextStatus === "Issued"
  }, [])

  const getNextStatuses = useCallback((status: string): string[] => {
    if (status === "Submitted") return ["Processing", "Cannot be Processed"]
    if (status === "Processing") return ["Issued", "Cannot be Processed"]
    return []
  }, [])

  const totalTeams = teams.length
  const totalMembers = useMemo(() => teams.reduce((acc, team) => acc + (team.members?.length || 0), 0), [teams])
  const queuePreview = useMemo(() => queue.results.slice(0, 6), [queue.results])
  const pendingDocuments = useMemo(() => documents.filter((doc) => doc.pending_review), [documents])
  const documentsNeedingUpload = useMemo(() => documents.filter((doc) => doc.requires_upload), [documents])
  const pendingCount = pendingDocuments.length
  const requiresUploadCount = documentsNeedingUpload.length
  const totalRequestsCount = requestsResponse?.count ?? 0
  const requestRows = requestsResponse?.results ?? []
  const hasNextRequests = Boolean(requestsResponse?.next)
  const hasPreviousRequests = Boolean(requestsResponse?.previous)

  const pageTitle = useMemo(() => {
    const name = user?.name?.split(" ")[0] || "Admin"
    return `Welcome, ${name}`
  }, [user?.name])

  const formatDocumentType = useCallback((type?: DocumentType) => {
    if (!type) return "Unknown"
    return type
      .replace(/_/g, " ")
      .replace(/\b\w/g, (char) => char.toUpperCase())
  }, [])

  const formatStatusLabel = useCallback((status?: string) => {
    if (!status) return "Unknown"
    return status
      .replace(/_/g, " ")
      .split(" ")
      .map((segment) => (segment ? segment[0].toUpperCase() + segment.slice(1) : segment))
      .join(" ")
  }, [])

  const resolveStatusVariant = useCallback((status?: string): "default" | "secondary" | "destructive" | "outline" => {
    if (!status) return "outline"
    const normalized = status.toLowerCase()
    if (["approved", "verified", "accepted"].includes(normalized)) return "default"
    if (["rejected", "declined", "invalid"].includes(normalized)) return "destructive"
    if (["pending", "submitted", "in_review", "processing", "awaiting_review", "pending_review"].includes(normalized)) {
      return "secondary"
    }
    return "outline"
  }, [])

  const applyStatusChange = useCallback(
    async (request: RequestData, targetStatus: string, remarks?: string) => {
      const trimmedRemarks = remarks?.trim() ?? ""
      const requiresRemarks = shouldCollectRemarks(request.status, targetStatus)

      if (requiresRemarks && !trimmedRemarks) {
        toast({
          variant: "destructive",
          title: "Remarks required",
          description: "Please add remarks before changing to this status.",
        })
        return
      }

      setStatusLoadingIds((prev) => (prev.includes(request.id) ? prev : [...prev, request.id]))

      try {
        await requestsService.changeRequestStatus(
          request.id,
          targetStatus,
          undefined,
          trimmedRemarks ? trimmedRemarks : undefined,
        )

        toast({
          title: "Status updated",
          description: `${request.team_name ? `${request.team_name} - ` : ""}Request marked as ${targetStatus}.`,
        })

        await Promise.all([loadRequests(requestsPage), loadHeadline()])
      } catch (error) {
        console.error("Status change failed", error)
        const message = error instanceof Error ? error.message : "Unable to update request status."
        toast({ variant: "destructive", title: "Action failed", description: message })
      } finally {
        setStatusLoadingIds((prev) => prev.filter((id) => id !== request.id))
        setStatusDialog(null)
      }
    },
    [shouldCollectRemarks, toast, loadRequests, requestsPage, loadHeadline],
  )

  const handleDownload = async (versionId: string) => {
    setDownloadingId(versionId)
    try {
      const { download_url } = await getDownloadURL(versionId)
      if (typeof window !== "undefined") {
        window.open(download_url, "_blank", "noopener,noreferrer")
      }
    } catch (error) {
      console.error("Download link error", error)
      const message = error instanceof Error ? error.message : "Unable to fetch download link."
      toast({ variant: "destructive", title: "Download failed", description: message })
    } finally {
      setDownloadingId(null)
    }
  }

  const handleApprove = async (versionId: string) => {
    setDocActionId(versionId)
    try {
      await approveDocument(versionId)
      toast({ title: "Document approved", description: "The team has been notified." })
      await Promise.all([loadHeadline(), loadTeamDocuments(selectedTeamId)])
    } catch (error) {
      console.error("Document approve failed", error)
      const message = error instanceof Error ? error.message : "Unable to approve document."
      toast({ variant: "destructive", title: "Action failed", description: message })
    } finally {
      setDocActionId(null)
    }
  }

  const handleReject = async () => {
    if (!rejectDialog) return
    const { versionId } = rejectDialog
    setDocActionId(versionId)
    try {
      await rejectDocument(versionId, rejectRemarks)
      toast({
        variant: "destructive",
        title: "Document rejected",
        description: rejectRemarks.trim() ? "Remarks shared with the team." : "The team has been notified.",
      })
      await Promise.all([loadHeadline(), loadTeamDocuments(selectedTeamId)])
      setRejectDialog(null)
      setRejectRemarks("")
    } catch (error) {
      console.error("Document reject failed", error)
      const message = error instanceof Error ? error.message : "Unable to reject document."
      toast({ variant: "destructive", title: "Action failed", description: message })
    } finally {
      setDocActionId(null)
    }
  }

  const closeRejectDialog = (open: boolean) => {
    if (!open) {
      setRejectDialog(null)
      setRejectRemarks("")
    }
  }

  return (
    <div className="space-y-8">
      <header className="flex flex-col gap-2 md:flex-row md:items-center md:justify-between">
        <div>
          <h1 className="flex items-center gap-2 text-2xl font-bold tracking-tight md:text-3xl" style={{ color: "#002449" }}>
            <ShieldCheck className="h-6 w-6 text-[#002449]" /> {pageTitle}
          </h1>
          <p className="text-sm text-muted-foreground">
            Quick snapshot of cohort progress, material requests, and compliance documents.
          </p>
        </div>
        <Button size="sm" variant="outline" className="gap-2" onClick={loadHeadline} disabled={refreshing}>
          {refreshing ? <Loader2 className="h-4 w-4 animate-spin" /> : <RefreshCcw className="h-4 w-4" />} Refresh data
        </Button>
      </header>

      {errors.length > 0 && (
        <div className="space-y-3">
          {errors.map((error, idx) => (
            <Card key={idx} className="flex items-start gap-3 border border-destructive/30 bg-destructive/5 p-4" role="alert">
              <AlertCircle className="mt-0.5 h-5 w-5 text-destructive" />
              <p className="text-sm text-destructive">{error}</p>
            </Card>
          ))}
        </div>
      )}

      <section className="grid grid-cols-1 gap-4 md:grid-cols-3">
        <Card className="p-5 shadow-sm">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-xs font-semibold uppercase tracking-wide text-muted-foreground">Total Teams</p>
              <p className="text-2xl font-bold" style={{ color: "#002449" }}>{totalTeams}</p>
            </div>
            <Users className="h-8 w-8 text-[#002449]" />
          </div>
          <p className="mt-3 text-xs text-muted-foreground">{totalMembers} participants currently onboard.</p>
        </Card>
        <Card className="p-5 shadow-sm">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-xs font-semibold uppercase tracking-wide text-muted-foreground">All Requests</p>
              <p className="text-2xl font-bold" style={{ color: "#002449" }}>{totalRequestsCount}</p>
            </div>
            <Layers className="h-8 w-8 text-[#002449]" />
          </div>
          <p className="mt-3 text-xs text-muted-foreground">Includes BOM, additional, and fabrication submissions.</p>
        </Card>
        <Card className="p-5 shadow-sm">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-xs font-semibold uppercase tracking-wide text-muted-foreground">Pending Documents</p>
              <p className="text-2xl font-bold" style={{ color: "#002449" }}>{pendingCount}</p>
            </div>
            <FileStackIcon />
          </div>
          <p className="mt-3 text-xs text-muted-foreground">
            {requiresUploadCount > 0
              ? `${requiresUploadCount} document${requiresUploadCount === 1 ? "" : "s"} awaiting first upload.`
              : "All required files have been submitted."}
          </p>
        </Card>
      </section>

      <section className="space-y-3">
        <div className="flex flex-col gap-2 sm:flex-row sm:items-center sm:justify-between">
          <h2 className="text-lg font-semibold" style={{ color: "#002449" }}>Virtual Queue Overview</h2>
          <div className="flex items-center gap-3">
            <Badge variant="outline">{queue.count} waiting</Badge>
            <Button asChild variant="outline" size="sm" className="text-xs">
              <Link href="/queue">Open queue</Link>
            </Button>
          </div>
        </div>
        <Card className="border border-gray-200">
          {loading && queuePreview.length === 0 ? (
            <div className="flex items-center justify-center p-6">
              <Loader2 className="h-5 w-5 animate-spin text-[#002449]" />
            </div>
          ) : queuePreview.length === 0 ? (
            <div className="p-6 text-sm text-muted-foreground">Queue is currently clear.</div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Team</TableHead>
                  <TableHead>Category</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Submitted</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {queuePreview.map((req) => (
                  <TableRow key={req.id}>
                    <TableCell className="font-medium" style={{ color: "#002449" }}>{req.team_name || req.team_id}</TableCell>
                    <TableCell className="text-xs text-muted-foreground">{req.category}</TableCell>
                    <TableCell>
                      <RequestStatusBadge status={req.status} />
                    </TableCell>
                    <TableCell className="text-xs text-muted-foreground">
                      {new Date(req.created_at).toLocaleString()}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
              <TableCaption>Visit the global queue for full progression controls.</TableCaption>
            </Table>
          )}
        </Card>
      </section>

      <section className="space-y-3">
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-semibold" style={{ color: "#002449" }}>All Requests Snapshot</h2>
          <div className="flex items-center gap-2">
            <Button
              variant="outline"
              size="sm"
              className="gap-1"
              onClick={() => setRequestsPage((prev) => Math.max(prev - 1, 1))}
              disabled={requestsLoading || !hasPreviousRequests}
            >
              <ChevronLeft className="h-4 w-4" /> Prev
            </Button>
            <span className="text-xs text-muted-foreground">Page {requestsPage}</span>
            <Button
              variant="outline"
              size="sm"
              className="gap-1"
              onClick={() => setRequestsPage((prev) => prev + 1)}
              disabled={requestsLoading || !hasNextRequests}
            >
              Next <ChevronRight className="h-4 w-4" />
            </Button>
          </div>
        </div>
        <Card className="border border-gray-200">
          {requestsLoading && requestRows.length === 0 ? (
            <div className="flex items-center justify-center p-6">
              <Loader2 className="h-5 w-5 animate-spin text-[#002449]" />
            </div>
          ) : requestsError ? (
            <div className="p-6 text-sm text-destructive">{requestsError}</div>
          ) : requestRows.length === 0 ? (
            <div className="p-6 text-sm text-muted-foreground">No requests filed yet.</div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Team</TableHead>
                  <TableHead>Category</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Submitted</TableHead>
                  <TableHead>Last Updated</TableHead>
                  <TableHead>Details</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {requestRows.map((request) => (
                  <TableRow key={request.id}>
                    <TableCell className="font-medium" style={{ color: "#002449" }}>{request.team_name || request.team_id}</TableCell>
                    <TableCell>
                      <RequestCategoryBadge category={request.category} />
                    </TableCell>
                    <TableCell>
                      <RequestStatusBadge status={request.status} />
                    </TableCell>
                    <TableCell className="text-xs text-muted-foreground">
                      {new Date(request.created_at).toLocaleString()}
                    </TableCell>
                    <TableCell className="text-xs text-muted-foreground">
                      {new Date(request.updated_at).toLocaleString()}
                    </TableCell>
                    <TableCell>
                      <Button asChild variant="link" className="px-0 text-xs text-[#002449]">
                        <Link href={`/my-requests/${request.id}`}>Open</Link>
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
              <TableCaption>Live feed of every material request submitted across teams.</TableCaption>
            </Table>
          )}
        </Card>
      </section>

      <section className="space-y-4">
        <div className="flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
          <div>
            <h2 className="text-lg font-semibold" style={{ color: "#002449" }}>Documents Verification</h2>
            <p className="text-xs text-muted-foreground">
              Select a team to review the latest uploads, approve compliant files, or reject with actionable remarks.
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Label htmlFor="team-selector" className="text-xs font-semibold uppercase tracking-wide text-muted-foreground">
              Team
            </Label>
            <Select
              value={selectedTeamId || undefined}
              onValueChange={(value) => setSelectedTeamId(value)}
              disabled={teams.length === 0}
            >
              <SelectTrigger id="team-selector" className="w-64 justify-between text-sm">
                <SelectValue placeholder="Select a team" />
              </SelectTrigger>
              <SelectContent className="max-h-72">
                {teams.map((team) => (
                  <SelectItem key={team.id} value={team.id}>
                    {team.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>
        <Card className="border border-gray-200">
          {teamDocsLoading ? (
            <div className="flex items-center justify-center p-6">
              <Loader2 className="h-5 w-5 animate-spin text-[#002449]" />
            </div>
          ) : teamDocsError ? (
            <div className="p-6 text-sm text-destructive">{teamDocsError}</div>
          ) : teamDocuments.length === 0 ? (
            <div className="p-6 text-sm text-muted-foreground">No documents have been uploaded by this team yet.</div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Document</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Last Upload</TableHead>
                  <TableHead>Remarks</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {teamDocuments.map((doc) => {
                  const latestVersion = doc.latest_version
                  const versionId = latestVersion?.id
                  const uploadedAt = latestVersion?.uploaded_at
                    ? new Date(latestVersion.uploaded_at).toLocaleString()
                    : doc.requires_upload
                      ? "Awaiting upload"
                      : "â€”"
                  const statusLabel = formatStatusLabel(doc.latest_status || latestVersion?.status)
                  const statusVariant = resolveStatusVariant(doc.latest_status || latestVersion?.status)
                  const isProcessing = docActionId === versionId
                  const isDownloading = downloadingId === versionId

                  return (
                    <TableRow key={`${doc.document_type}-${doc.document_id || "pending"}`}>
                      <TableCell>
                        <div className="font-medium" style={{ color: "#002449" }}>{formatDocumentType(doc.document_type)}</div>
                        {latestVersion?.version && (
                          <div className="text-xs text-muted-foreground">Version {latestVersion.version}</div>
                        )}
                      </TableCell>
                      <TableCell>
                        <Badge variant={statusVariant}>{statusLabel}</Badge>
                      </TableCell>
                      <TableCell className="text-xs text-muted-foreground">{uploadedAt}</TableCell>
                      <TableCell className="text-xs text-muted-foreground">
                        {latestVersion?.rejection_reason || "â€”"}
                      </TableCell>
                      <TableCell>
                        {versionId ? (
                          <div className="flex flex-wrap gap-2">
                            <Button
                              size="sm"
                              variant="outline"
                              className="text-xs"
                              onClick={() => handleDownload(versionId)}
                              disabled={isDownloading || isProcessing}
                            >
                              {isDownloading ? (
                                <Loader2 className="h-4 w-4 animate-spin" />
                              ) : (
                                <DownloadIcon className="h-4 w-4" />
                              )}
                              Download
                            </Button>
                            <Button
                              size="sm"
                              variant="secondary"
                              className="text-xs"
                              onClick={() => handleApprove(versionId)}
                              disabled={isProcessing}
                            >
                              {isProcessing ? <Loader2 className="h-4 w-4 animate-spin" /> : <Check className="h-4 w-4" />}
                              Approve
                            </Button>
                            <Button
                              size="sm"
                              variant="destructive"
                              className="text-xs"
                              onClick={() =>
                                setRejectDialog({
                                  versionId,
                                  teamName: teams.find((team) => team.id === selectedTeamId)?.name || "Team",
                                  documentLabel: formatDocumentType(doc.document_type),
                                })
                              }
                              disabled={isProcessing}
                            >
                              {isProcessing ? <Loader2 className="h-4 w-4 animate-spin" /> : <XIcon className="h-4 w-4" />}
                              Reject
                            </Button>
                          </div>
                        ) : (
                          <span className="text-xs text-muted-foreground">Awaiting first upload</span>
                        )}
                      </TableCell>
                    </TableRow>
                  )
                })}
              </TableBody>
              <TableCaption>Decisions notify the team instantly over email.</TableCaption>
            </Table>
          )}
        </Card>
      </section>

      <Dialog open={Boolean(rejectDialog)} onOpenChange={closeRejectDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Reject document</DialogTitle>
            <DialogDescription>
              Leave remarks so the team knows what needs to be corrected before they upload again.
            </DialogDescription>
          </DialogHeader>
          {rejectDialog && (
            <div className="space-y-3">
              <p className="text-sm font-medium" style={{ color: "#002449" }}>
                {rejectDialog.documentLabel} â€” {rejectDialog.teamName}
              </p>
              <div className="space-y-2">
                <Label htmlFor="reject-remarks" className="text-xs font-semibold uppercase tracking-wide text-muted-foreground">
                  Remarks (required when rejecting)
                </Label>
                <Textarea
                  id="reject-remarks"
                  placeholder="Explain what needs to be fixed or uploaded again"
                  value={rejectRemarks}
                  onChange={(event) => setRejectRemarks(event.target.value)}
                />
              </div>
            </div>
          )}
          <DialogFooter className="gap-2">
            <Button variant="outline" onClick={() => closeRejectDialog(false)}>
              Cancel
            </Button>
            <Button
              variant="destructive"
              onClick={handleReject}
              disabled={!rejectRemarks.trim() || (rejectDialog && docActionId === rejectDialog.versionId)}
            >
              {rejectDialog && docActionId === rejectDialog.versionId ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : (
                <XIcon className="mr-2 h-4 w-4" />
              )}
              Reject document
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}

function FileStackIcon() {
  return (
    <svg
      width="32"
      height="32"
      viewBox="0 0 24 24"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      className="text-[#002449]"
    >
      <path
        d="M6 3h9l5 5v11a2 2 0 0 1-2 2H6a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2Z"
        stroke="currentColor"
        strokeWidth="1.5"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
      <path
        d="M9 7h3m-3 4h6m-6 4h6"
        stroke="currentColor"
        strokeWidth="1.5"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  )
}

export default AdminDashboard
